:root {
  --font-primary: "Nunito Sans", sans-serif;
}

body::-webkit-scrollbar {
  width: 0.75rem;
}
body::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 5px;
}
body::-webkit-scrollbar-thumb {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.5);
  border-radius: 10px;
}

.modal::-webkit-scrollbar {
  width: 0.5rem;
}

:root {
  --bg-color: hsl(0, 0%, 98%);
  --elements-color: hsl(0, 0%, 100%);
  --text-color: hsl(200, 15%, 8%);
  --input-color: hsl(0, 0%, 52%);
  --shadow-primary: 0 1px 4px rgb(133, 133, 133, 0.3);
  --shadow-secondary: 0 2px 10px rgb(133, 133, 133, 0.2);
  --shadow-btn: 0 2px 7px rgb(133, 133, 133, 0.5);
  --hover-effect-color: hsl(0, 0%, 60%);
  --btn-hover: hsl(0, 0%, 90%);
  --btn-scroll-top: hsl(0, 0%, 79%);
}

body.dark-theme {
  --bg-color: hsl(207, 26%, 17%);
  --elements-color: hsl(209, 23%, 22%);
  --text-color: hsl(0, 0%, 100%);
  --input-color: hsl(0, 0%, 100%);
  --shadow-primary: 0px 1px 8px 0px rgb(0, 0, 0, 0.1);
  --shadow-secondary: 0px 1px 15px 0px rgb(0, 0, 0, 0.1);
  --shadow-btn: 0px 1px 15px 0px rgb(0, 0, 0, 0.3);
  --btn-hover: hsl(209, 23%, 17%);
  --btn-scroll-top: hsl(209, 26%, 20%);
}

html {
  font-size: 100%;
  scroll-behavior: smooth;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-primary);
  background-color: var(--bg-color);
  min-height: 100vh;
  transition: background 0.3s linear, color 0.3s linear;
}

main {
  padding: 2rem 0;
}

img {
  display: block;
}

.btn {
  background-color: var(--elements-color);
  color: var(--text-color);
  font-weight: 600;
  border-radius: 0.25rem;
  box-shadow: var(--shadow-btn);
  transition: all ease 0.5s;
  cursor: pointer;
}
.btn:hover {
  color: var(--hover-effect-color);
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

.container {
  width: 90vw;
  max-width: 75rem;
  margin: 0 auto;
}

.countries {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(270px, 1fr));
  gap: 2rem;
  justify-items: center;
}

.header {
  background-color: var(--elements-color);
  box-shadow: var(--shadow-primary);
}

.nav-menu {
  display: flex;
  justify-content: space-between;
  padding: 1.25rem 0;
}
.nav-menu__link {
  font-size: 1rem;
  color: var(--text-color);
}
@media (min-width: 48em) {
  .nav-menu__link {
    font-size: 1.5rem;
  }
}
.nav-menu__theme {
  font-family: inherit;
  font-size: 1rem;
  font-weight: 600;
  color: var(--text-color);
  display: flex;
  justify-content: initial;
  align-items: center;
  gap: 0.3125rem;
  background: transparent;
  border: none;
  cursor: pointer;
}
.nav-menu__theme span {
  font-size: 0.875rem;
}
@media (min-width: 48em) {
  .nav-menu__theme span {
    font-size: 1rem;
  }
}
@media (min-width: 48em) {
  .nav-menu__theme {
    font-size: 1.25rem;
  }
}

.search {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  gap: 2rem;
  margin-bottom: 2rem;
}
@media (min-width: 48em) {
  .search {
    flex-direction: row;
  }
}

.form {
  position: relative;
  color: var(--input-color);
  display: flex;
  justify-content: initial;
  align-items: center;
  flex: 1;
  max-width: 31.25rem;
}
.form__input {
  flex: 1;
  background-color: var(--elements-color);
  color: var(--text-color);
  box-shadow: var(--shadow-primary);
  padding: 1rem;
  padding-left: 3.75rem;
  border: none;
  border-radius: 0.5rem;
  outline: var(--elements-color);
}
.form__input::placeholder {
  color: var(--input-color);
  opacity: 0.8;
}
.form__icon {
  position: absolute;
  font-size: 1.25rem;
  left: 1.25rem;
}

.dropdown {
  display: flex;
  justify-content: space-between;
  flex: 1;
  align-items: center;
  font-family: inherit;
  font-weight: 600;
  position: relative;
  max-width: 11.875rem;
  border: none;
  border-radius: 0.5rem;
  padding: 1rem 1.5rem;
  cursor: pointer;
}
.dropdown, .dropdown__list {
  background-color: var(--elements-color);
  color: var(--text-color);
  box-shadow: var(--shadow-secondary);
}
.dropdown__icon {
  font-size: 1.25rem;
  transition: all ease 0.5s;
}
.dropdown__icon--active {
  transform: rotate(180deg);
}
.dropdown__list {
  position: absolute;
  width: 100%;
  top: 110%;
  right: 0;
  z-index: 1;
  border-radius: 0.5rem;
  padding: 1rem 2rem;
  opacity: 0;
  transform-origin: top;
  transform: scaleY(0);
  transition: all ease 0.5s;
}
.dropdown__list--active {
  opacity: 1;
  transform: scaleY(1);
}
.dropdown__item {
  text-align: start;
  padding: 0.5rem;
  transition: all ease 0.5s;
}
.dropdown__item:hover, .dropdown__item--active {
  color: var(--hover-effect-color);
  margin-left: 0.3125rem;
}

.card {
  width: 16.875rem;
  border-radius: 0.5rem;
  overflow: hidden;
  background-color: var(--elements-color);
  box-shadow: var(--shadow-primary);
  cursor: pointer;
}
.card__img {
  height: 12.5rem;
  object-fit: cover;
  width: 100%;
}
.card__content {
  color: var(--text-color);
  padding: 2rem 2rem 3rem;
}
.card__name {
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-bottom: 1rem;
}
.card__list {
  display: flex;
  justify-content: initial;
  flex-direction: column;
  gap: 0.5rem;
}
.card__item span {
  font-weight: 600;
}

.modal {
  position: fixed;
  top: 0;
  bottom: 0;
  width: 100%;
  background-color: var(--bg-color);
  color: var(--text-color);
  z-index: 10;
  visibility: hidden;
  transform: translateX(-100%);
  transition: all ease 0.5s;
  overflow-y: scroll;
}
@media (min-width: 48em) {
  .modal {
    opacity: 0;
    transform: none;
  }
}
.modal--active {
  visibility: visible;
  transform: translateX(0);
}
@media (min-width: 48em) {
  .modal--active {
    opacity: 1;
    transform: none;
  }
}
.modal--btn {
  font-family: inherit;
  font-weight: 600;
  border: none;
  color: var(--text-color);
  background-color: var(--elements-color);
  padding: 0.5rem 1.5rem;
  border-radius: 0.25rem;
  box-shadow: var(--shadow-btn);
  cursor: pointer;
  transition: all ease 0.3s;
}
.modal--btn:hover {
  background-color: var(--btn-hover);
}
.modal__overlay {
  padding: 2rem 0;
}
.modal__back {
  display: flex;
  justify-content: initial;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 3rem;
}
.modal__back-icon {
  font-size: 1.125rem;
}
.modal__container {
  display: grid;
  gap: 2rem;
}
@media (min-width: 64em) {
  .modal__container {
    grid-template-columns: repeat(2, minmax(28.125rem, 34.375rem));
    justify-content: space-between;
  }
}
.modal__flag {
  width: 100%;
  max-height: 20.625rem;
  object-fit: cover;
}
.modal__name {
  margin-bottom: 1rem;
}
.modal__info {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(14.375rem, 1fr));
  gap: 2.5rem;
}
@media (min-width: 64em) {
  .modal__info {
    grid-template-columns: repeat(2, 1fr);
  }
}
.modal__list {
  display: flex;
  justify-content: initial;
  flex-direction: column;
  gap: 0.5rem;
}
.modal__item span {
  font-weight: 600;
}
.modal__border {
  margin-top: 3rem;
  display: flex;
  justify-content: initial;
  flex-direction: column;
  gap: 1rem;
}
.modal__border-name {
  font-size: 1.25rem;
}
.modal__border-list {
  display: flex;
  justify-content: initial;
  flex-wrap: wrap;
  gap: 0.5rem;
}
.modal__border-btn {
  padding: 0.5rem 2rem;
}
.modal__iframe {
  width: 100%;
  height: 17.5rem;
}

.btn-scroll {
  position: fixed;
  bottom: 1.25rem;
  right: 1rem;
  width: 2.1875rem;
  height: 2.1875rem;
  background-color: var(--btn-scroll-top);
  clip-path: circle();
  border: none;
  visibility: hidden;
  opacity: 0;
  transform: translateY(100%);
  cursor: pointer;
  transition: all ease 0.5s;
}
.btn-scroll:hover {
  background-color: var(--hover-effect-color);
}
.btn-scroll--active {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}
.btn-scroll__link {
  display: inline-block;
}
.btn-scroll__link i {
  font-size: 1.5rem;
  color: whitesmoke;
}

.not-found {
  background: url(../assets/country-not-found.svg) no-repeat;
  background-position: center;
  background-size: contain;
  transform: scale(0);
  opacity: 0;
  transition: all ease 1s;
}
.not-found--active {
  height: 15.625rem;
  transform: scale(1);
  opacity: 1;
}
@media (min-width: 48em) {
  .not-found--active {
    height: 18.75rem;
  }
}